  .movies-detail {
    display: flex;
    justify-content: space-between;
    flex-wrap:  wrap;
    padding:50px;
    width: 100%;
  }
  
  .movies-detail .movie-detail {
    width: 100%;
    background-color: white;
    margin: 0px 0px 70px 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color:#2c2c2c;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  }
  
  .movies-detail .movie-detail a {
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
  }
  
  .movie-detail img {
    position: relative;
    top: -50px;
    max-width: 300px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  }
  
  .movie-detail .movie_title-detail,
  .movie-detail .movie_year-detail {
    margin: 0;
    font-weight: 300;
  }
  
  .movie-detail .movie_title-detail {
    margin-bottom: 5px;
    font-size: 32px;
    color: #2c2c2c;
  }
  
  .movie-detail .movie_genres-detail {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
  }
  
  .movie_genres-detail li,
  .movie-detail .movie_year-detail {
    margin-right: 10px;
    font-size: 20px;
  }